<template>
  <div class="meeting">
    <div class="meeting-row">
      <div class="meeting-col">
        <van-icon class="iconfont" size="2rem" color="#fff" class-prefix='icon' name='jishitaolun'></van-icon>
        <div>
          <van-button id="createMeetingBtn" color="#999" round type="default">召开会议</van-button>
        </div>
      </div>
      <div class="meeting-col">
        <van-icon class="iconfont" size="2rem" color="#FFFFFF" class-prefix='icon' name='jiarujiazu'></van-icon>
        <div>
          <van-button color="#999" round type="default">加入会议</van-button>
        </div>
      </div>
    </div>
    <div class="meeting-row">
      <div class="meeting-col">
        <van-icon class="iconfont" size="2rem" color="#FFFFFF" class-prefix='icon' name='yuyue'></van-icon>
        <div>
          <van-button color="#999" round type="default">预约会议</van-button>
        </div>
      </div>
      <div class="meeting-col">
        <van-icon class="iconfont" size="2rem" color="#FFFFFF" class-prefix='icon' name='meeting-management-my-meeting'></van-icon>
        <div>
          <van-button color="#999" round type="default">我的会议</van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { NavBar, Icon, Button } from 'vant'

export default {
  components: {
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [Icon.name]: Icon
  },
  created () {
    const app = {
      meetingId: '',
      // Application Constructor
      initialize: function () {
        document.addEventListener('deviceready', app.onDeviceReady, false)
      },
      onDeviceReady: function () {
        var config = {
          npsUrl: 'http://175.102.132.82:8018/nps_x1/',
          npsSlaveUrl: 'http://175.102.132.82:8018/nps_x1/',
          account: '67000101',
          nickname: 'hahaha',
          appKey: '62be9552972140c5b77aafaedbfa3229',
          password: '123456'
        }
        var that = this
        // eslint-disable-next-line no-undef
        cicdi.plugins.meeting.initSDK(function () {
          document.getElementById('createMeetingBtn').addEventListener('click', that.createMeeting)
        }, function () {
          alert('error')
        }, config)
      },
      createMeeting: function () {
        var meetingConfig = {
          topic: '部门例会',
          meetingType: 1
        }
        // eslint-disable-next-line no-undef
        cicdi.plugins.meeting.createMeeting(function (meetingId) {
          app.meetingId = meetingId
          // document.getElementById('joinMeetingBtn').style.display = 'block'
          // document.getElementById('getPendingMeetingBtn').style.display = 'block'
          // document.getElementById('deletePendingMeetingBtn').style.display = 'block'
          // document.getElementById('joinMeetingBtn').addEventListener('click', app.joinMeeting)
          // document.getElementById('getPendingMeetingBtn').addEventListener('click', app.getPendingMeetingID)
          // document.getElementById('deletePendingMeetingBtn').addEventListener('click', app.deletePendingMeetingID)
        }, function () { alert(1) }, meetingConfig)
      }
    }
    app.initialize()
  }
}
</script>

<style  scoped>
.meeting{
  height:calc(100vh - 46px);
  background-color: #028FD3;
}
.meeting-row{
  height: 200px;
  /* background-color: #ccc; */
  display: flex;
  justify-content: space-between;
}
.meeting-row:first-child{
  padding-top: 200px;
}
.meeting-col{
  text-align: center;
  width: 48%;
}
</style>



